	module.exports = {"data":{"contentfulPouvoir":{"nomPouvoir":"Adaptation","description":{"childMarkdownRemark":{"html":"<p>Le pouvoir d'adaptation permet au possesseur d'adapter une partie de son corps à une texture à laquelle il est en contact, ce qui permet d'absorber certaines caractéristiques du matériel. Comme la résistance du métal ou l'élasticité du caoutchouc. Au départ, plusieurs limitations sont impliquées, la masse du matériel dupliqué ne peut pas être supérieure à celle du matériel d'origine. Plusieurs matériaux ne peuvent pas être absorbé en même temps. Le matériel répliqué ne peut pas être dupliqué. Le contact doit se faire avec le bout des doigts, où se trouvent les empreintes digitales. Mais avec de la pratique et de l'apprentissage, ses limitations peuvent être franchies ou réduites.</p>"}},"equivalentUrl":"/powers/adaptation"}},"pathContext":{"slug":"adaptation"}}

